import React from 'react'
import gatocheeto from "../assets/gatocheeto.jpg";
import cheeto from "../assets/cheeto.png";
import { NavLink } from "react-router-dom";

const Cheeto = () => {
    return (
        <div className="container mx-auto p-6 md:px-28 md:pt-20">
            <div className="flex flex-wrap mb-12">
                <div className="w-full lg:w-2/5 px-2 mb-4">
                    <div className="h-auto items-center justify-center">
                        <img alt="Me agradas toma un cheeto" className="mx-auto" src={gatocheeto} />
                    </div>
                </div>
                <div className="w-full lg:w-3/5 px-2 mb-4">
                    <div className="items-center h-auto mt-20">
                        <NavLink to="/" exact activeClassName="active">
                            <img alt="Me agradas toma un cheeto" className="w-32 mx-auto" src={cheeto} />
                        </NavLink>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Cheeto
