import React, { useState, useEffect } from 'react';
const Age = () => {
  const [age, setAge] = useState();

  const tick = () => {
    const divisor = 1000 * 60 * 60 * 24 * 365.2421897; // ms in an average year
    const birthTime = new Date('1999-02-15T07:25:00');
    setAge(((Date.now() - birthTime) / divisor).toFixed(10));
  };

  useEffect(() => {
    const timer = setInterval(() => tick(), 25);
    return () => {
      clearInterval(timer);
    };
  }, []);
  return <>{age}</>;
};

const data = [
  {
    id: '1',
    type: 'info',
    title: "Edad actual",
    content: <Age />,
  },
  {
    id: '2',
    type: 'info',
    title: "Ciudad",
    content: "Las Palmas de Gran Canaria",
  },
  {
    id: '3',
    type: 'info',
    title: "Carnet de conducir",
    content: "B",
  },
  {
    id: '4',
    type: 'info',
    title: "Ingl√©s",
    content: "B2",
  },
];

export default data;
