import React, { useState } from "react";
import data from "../data/education";
import { ArrowNarrowRightIcon } from '@heroicons/react/solid';

const Educacion = () => {
  const [items] = useState(data);
  return (
    <div className="grid grid-cols-1 sm:grid-cols-2 gap-10 mb-12">
      <div>
        <div className="flex gap-2 mb-10">
          <ArrowNarrowRightIcon className="h-6 w-6 text-gray-400 my-auto"></ArrowNarrowRightIcon>
          <h1 className="font-bold text-white text-2xl sm:text-3xl">
            Estudios
          </h1>
        </div>
        {items.map((item) => {
          const { id, title, link, place, year } = item;
          if (item.type === "education") {
            return (
              <div key={id} className="mb-6">
                <a href={link} className="text-malibu font-bold italic rrss">
                  {place}
                </a>
                <p className="text-white font-medium text-lg tracking-wide">
                  {title}
                </p>
                <p className="my-auto font-bold text-shark-100 tracking-wide">
                  {year}
                </p>
              </div>
            );
          }
          return true;
        })}
      </div>
      <div>
        <div className="flex gap-2 w-full mb-10">
          <ArrowNarrowRightIcon className="h-6 w-6 text-gray-400 my-auto"></ArrowNarrowRightIcon>
          <h1 className="font-bold text-white text-2xl sm:text-3xl">
            Experiencia
          </h1>
        </div>
        {items.map((item) => {
          const { id, title, link, place, year } = item;
          if (item.type === "experience") {
            return (
              <div key={id} className="mb-6">
                <a href={link} className="text-malibu font-bold italic rrss">
                  {place}
                </a>
                <p className="text-white font-medium text-lg tracking-wide">
                  {title}
                </p>
                <p className="my-auto font-bold text-shark-100 tracking-wide">
                  {year}
                </p>
              </div>
            );
          }
          return true;
        })}
      </div>
    </div>
  );
};

export default Educacion;
