import React, { useState } from "react";
import data from "../data/skills";
import { ArrowNarrowRightIcon } from '@heroicons/react/solid';

const Aptitudes = () => {
  const [items] = useState(data);
  return (
    <div className="py-4">
      <div className="flex gap-2 w-full  mb-10">
        <ArrowNarrowRightIcon className="h-6 w-6 text-gray-400 my-auto"></ArrowNarrowRightIcon>
        <h1 className="font-bold text-white text-2xl sm:text-3xl">Mis herramientas</h1>
      </div>
      <div className="w-full flex flex-wrap bg-grey-light gap-4">
        {items.map((item) => {
          const { id, title } = item;
          if (item.type === "skill") {
            return (
              <div key={id} className="my-auto py-1 px-2 md:py-1 md:px-3 border bg-shark-400 border-malibu">
                <h3 className="font-xs md:font-medium text-malibu">{title}</h3>
              </div>
            );
          }
          return true;
        })}
      </div>
    </div>
  );
};

export default Aptitudes;
